body {
  margin: 0;
}
.container {
  width: 100vw;
  height: 100vh;
  background: black;
}

.textdiv {
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  width: 100vw;
  height: 100vh;
}

.text {
  padding: auto;
  text-align: center;
  font-size: 50px;
  font-weight: 800;
  align-items: center;
  justify-content: center;
}

.btndiv {
  display: flex;
  gap: 5px;
  justify-content: center;
  align-items: center;
  height: 50px;
}
.btn {
  width: 80px;
  padding: 2px 2px;
  height: 40px;
  font-size: 20px;
  border-radius: 7px;
  border-width: 1;
  background-color: black;
  border-color: gray;
}

.setbtn {
  background: black;
  color: white;
}

.black {
  background: white;
  color: black;
}

.white {
  background: black;
  color: white;
}

.blue {
  color: #3800fd;
  background: black;
}

.red {
  color: #ff0000;
  background: black;
}

.yellow {
  color: #ccff00;
  background: black;
}

.pink {
  color: #ff69b4;
  background: black;
}

.green {
  color: #39ff14;
  background: black;
}
